* {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body {

}

    //colors
    $white: #ffffff;
    $black: #000000;
    $gold: #d6954b;
    $lgray: #fbfbfb;
    $gray: #f4f4f4;
    $dgray: #4a4a4a;
    $mdgray: #dbdbdb;
    $danger: red;
    $warn: yellow;
    $success: green;
    $info: blue;
    //fonts
    $serif: "cormorant-garamond", serif;
    $sans-serif: "proxima-nova", sans-serif;
    //font-weigth
    $fw-light: 300;
    $fw-regular: 400;
    $fw-medium: 600;
    //font-size
    $fz-descriprion: .725rem;
    $fz-title-main: 3rem;
    $fz-title: 2.5rem;
    $fz-text: 0.88rem;


%reference {
    color: $gold;
}